
@mixin cf {
	&:before,
	&:after {
		content: ' ';
		display: table;
	}

	&:after {
		clear: both;
	}
}

* {
  box-sizing: border-box;
}

html {
	background: #0cb0b1;
	text-align: center;
}

.hidden {
	display: none !important;
}

.sizeGuide {
  @include cf;
  display: inline-block;
  margin: 50px auto;
  padding: 40px 30px 100px;
  font-family: Arial;
  background: #f4f4f4;
  border-radius: 3px;
  box-shadow: 0px 5px 10px #a1a1a1;

  header {
    display: block;
	position: relative;
    color: #777;
	z-index: 10;

    div {
      float: left;
      position: relative;
      width: 100px;
      padding: 0px 0 50px;
      text-align: center;

      span {
        font-size: 12px;
        font-weight: 900;
        text-transform: uppercase;
        transition: color .3s;
      }

      .prev,
      .next{
        font-family: "FontAwesome";
        position: absolute;
        left: 50%;
        padding: 10px;
        margin-left: -22px;
        width: auto;
        font-size: 25px;
        font-weight: bold;
        cursor: pointer;
        transition: color .2s;

        &:not(.disabled):hover {
          color: #0cb0b1;
        }

		&.disabled {
			opacity: 0.15;
		}

      }

      .prev {
        bottom: 5px;
        &:before {
          content:"\f077";
        }
      }

      .next {
        bottom: -75px;
        &:before {
          content:"\f078";
        }
      }

    }
  }

  .content {
    @include cf;
    clear: both;

    [data-filter] {
	  display: block;
	  float: left;
      position: relative;
      width: 100px;
	  min-height: 1px;
      text-align: center;
      font-size: 20px;
      font-weight: 900;

      [data-country] {
        opacity: 0;
        &.selected {
          opacity: 1;
        }
      }

      [data-id] {
        display: block;
		position: absolute;
		width: 100%;
		opacity: 0;
		top: 0;
		transition: all 0.3s;

        &.selected {
			opacity: 1;
        }

		&.selected-prev,
		&.selected-prev-prev,
		&.selected-next,
		&.selected-next-next {
			transform: scale(.7);
		}

		&.selected-prev,
		&.selected-next {
			transform: scale(.7);
			opacity: 0.15;
		}

		&.selected-prev {
			top: -18px;
		}

		&.selected-next {
			top: 18px;
		}

		&.selected-prev-prev {
			top: -24px;
		}

		&.selected-next-next {
			top: 24px;
		}

      }
    }

    *[data-country] {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
	  min-height: 1px;
    }

  }

}

.options {
  @include cf;
  width: 664px;
  display: block;
  text-align: left;
  margin: 50px auto;
  padding: 40px 30px;
  font-family: Arial;
  background: #f4f4f4;
  border-radius: 3px;
  box-shadow: 0px 5px 10px #a1a1a1;

  h2 {
	  text-transform: uppercase;
	  color: #0cb0b1;
	  margin: 0 0 5px;
	  padding: 0;
	  line-height: 20px;
	  font-weight: 700;
  }

  p.description {
	  font-size: 12px;
	  color: #aaa;
	  font-weight: 700;
	  margin: 0 0 30px;
	  text-transform: uppercase;
  }

}

.buttonBox {
	display: inline-block;
	text-align: center;

	.label {
		display: block;
		margin-bottom: 10px;
		font-family: Arial;
		color: #888;
		font-size: 12px;
		font-weight: 900;
		text-transform: uppercase;
	}
}

.switch {
	position: relative;
	display: block;
	margin: 0 auto;
	width: 60px;
	height: 34px;

	.slider {
		position: absolute;
		display: block;
		cursor: pointer;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border-radius: 34px;
		background-color: #ccc;
		-webkit-transition: .4s;
		transition: .4s;

		&:before {
			position: absolute;
			content: "";
			border-radius: 50%;
			height: 26px;
			width: 26px;
			left: 4px;
			bottom: 4px;
			background-color: white;
			-webkit-transition: .4s;
			transition: .4s;
		}
	}


	input {
		display:none;

		&:checked + .slider {
			background-color: #2196F3;
		}

		&:focus + .slider {
			box-shadow: 0 0 1px #2196F3;
		}

		&:checked + .slider:before {
			-webkit-transform: translateX(26px);
			-ms-transform: translateX(26px);
			transform: translateX(26px);
		}
	}

}
